@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap');


.body {
    background-color: #eff3f5;
}

.app-wrapper {
    max-width: 2200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
            "h h"
    "n c";
    grid-template-rows: 80px 1fr;
    grid-template-columns: 1fr 11fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    group-reset: c;
    min-height: calc(100vh - 355px);
    /*min-height: 100vh;*/
}


