.header {
    /*grid-area: h;*/
    /*background-color: #232f42;*/
    display: flex;
    align-items: center;
    width: 100%;
}

.header i {
    color: #9b1815;
    margin-left: 10px;
    font-size: 40px;
    font-weight: 700;
    font-family: 'Dancing Script', sans-serif;
}

.header img {
    width: 83px;
    height: 70px;
    padding: 5px;
    margin-left: 15px;
}

.leftMenu {
    display: flex;
    margin: 0 20px 0 auto;
    align-items: center;
}

.searchForm {
    width: 100%;
    position: relative;
    overflow: hidden;
    margin: 0 20px;
}

.search {
    border: none;
    background-color: #2e3d55;
    width: 100%;
    height: 50px;
    padding-left: 20px;
    padding-right: 100px;
    outline: none;
    color: #718098;
    font-size: 16px;
}

.searchButton {
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    padding: 0;
    width: 20px;
    height: 20px;
    background-image: url("../../img/search.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20px;
}

.link {
    cursor: pointer;
    width: 25px;
    height: 25px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 15px;
    padding: 5px;
    display: inline-block;
    position: relative;
}

.link span {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(30%, -27%);
    font-size: 14px;
    color: white;
    font-weight: 700;
    text-align: center;
}

.linkPerson {
    background-image: url("../../img/1.PNG");
}

.linkPerson span {
    background-color: #ff62c2;
}

.linkMessages {
    background-image: url("../../img/2.PNG");
}

.linkMessages span {
    background-color: #44d775;
}

.linkNotification {
    background-image: url("../../img/3.PNG");
}

.linkNotification span {
    background-color: #ff6f43;
}

.linkProfile img {
    width: 55px;
    height: 55px;
    border-radius: 100%;
}
