.nav {
    grid-area: n;
    background-color: #232f42;
}

.item {
    display: flex;
    box-sizing: border-box;
    position: relative;
    color: #718098;
    font-weight: 700;
    text-decoration: none;
    user-select: none;
    padding: 1em;
    width: 100%;
    text-align: start;
    text-transform: uppercase;
    outline: none;
}

.icon {
    margin-right: 1em;
}

.item:active {
    top: .1em;
    box-shadow: 0 0 0 60px rgba(46, 61, 85, 1) inset;
}