.paginator {
    margin: 5px;
}
.pageNumber {
    display: inline-block;
    width: 25px;
    height: 25px;
    text-align: center;
    border-radius: 100%;
    border: 1px solid #718098;
    margin: 5px;
    cursor: pointer;
    position: relative;
}
.pageNumber:hover{
    color: #ff6f43;
    top: .1em;
}

.pageNumber.selectedPage {
    font-weight: bold;
    border-color: #232F42;
}